<template>
    <div>
        <loading :active.sync="isLoading" ></loading>
        <div class="text-right my-4">
            <button class="btn btn-primary" @click.prevent="openModal(true)">建立新商品</button>
        </div>
        <table class="table">
            <thead>
                <tr>
                    <th width="100">分類</th>
                    <th>名稱</th>
                    <th width="100">原價</th>
                    <th width="100">售價</th>
                    <th width="120">是否啟用</th>
                    <th width="150">編輯</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in products" :key="item.id">
                    <td>{{item.category}}</td>
                    <td>{{item.title}}</td>
                    <td class="text-right">{{item.origin_price | currency}}</td>
                    <td class="text-right">{{item.price | currency}}</td>
                    <td>
                        <div v-if="item.is_enabled" class="text-success text-center">已啟用</div>
                        <div v-else class="text-muted text-center">尚未啟用</div>
                    </td>
                    <td>
                        <div class="btn-group mr-2 " role="group" aria-label="First group">
                            <button type="button" @click.prevent="openModal(false,item)" class="btn btn-sm btn-outline-info">編輯</button>
                            <button type="button" @click.prevent="removeProduct(item.id)" class="btn btn-sm btn-danger">刪除</button>
                        </div>
                    </td>
                </tr>
            </tbody>
        </table>
        <!-- Pagination -->
        <Pagination :pagination="pagination" v-on:getPageProducts="getProducts"/>
        <!-- Modal -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header bg-light">
                    <h5 class="modal-title text-secondary" id="productModalLabel">新增商品</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                        <div class="col-sm-4">
                            <div class="form-group">
                            <label for="image">輸入圖片網址</label>
                            <input type="text" class="form-control" id="image"
                                v-model="temProduct.imageUrl" placeholder="請輸入圖片連結">
                            </div>
                            <div class="form-group">
                            <label for="customFile">或 上傳圖片
                                <i class="fas fa-circle-notch fa-spin" v-if="status.fileLoading"></i>
                            </label>
                            <input type="file" id="customFile" class="form-control"
                                @change="uploadFile" ref="files">
                            </div>
                            <img class="img-fluid" :src="temProduct.imageUrl" alt="">
                        </div>
                        <div class="col-sm-8">
                            <div class="form-group">
                            <label for="title">標題</label>
                            <input type="text" class="form-control" id="title"
                                v-model="temProduct.title" placeholder="請輸入標題">
                            </div>

                            <div class="form-row">
                            <div class="form-group col-md-6">
                                <label for="category">分類</label>
                                <input type="text" class="form-control" id="category"
                                v-model="temProduct.category" placeholder="請輸入分類">
                            </div>
                            <div class="form-group col-md-6">
                                <label for="price">單位</label>
                                <input type="unit" class="form-control" id="unit"
                                v-model="temProduct.unit" placeholder="請輸入單位">
                            </div>
                            </div>

                            <div class="form-row">
                            <div class="form-group col-md-6">
                                <label for="origin_price">原價</label>
                                <input type="number" class="form-control" id="origin_price"
                                v-model="temProduct.origin_price" placeholder="請輸入原價">
                            </div>
                            <div class="form-group col-md-6">
                                <label for="price">售價</label>
                                <input type="number" class="form-control" id="price"
                                v-model="temProduct.price" placeholder="請輸入售價">
                            </div>
                            </div>
                            <hr>

                            <div class="form-group">
                            <label for="description">產品描述</label>
                            <textarea type="text" class="form-control" id="description"
                            v-model="temProduct.description" placeholder="請輸入產品描述"></textarea>
                            </div>
                            <div class="form-group">
                            <label for="content">說明內容</label>
                            <textarea type="text" class="form-control" id="content"
                            v-model="temProduct.content" placeholder="請輸入產品說明內容"></textarea>
                            </div>
                            <div class="form-group">
                            <div class="form-check">
                                <input class="form-check-input" type="checkbox"
                                v-model="temProduct.is_enabled"
                                :true-value="1" :false-value="0" id="is_enabled">
                                <label class="form-check-label" for="is_enabled">
                                是否啟用
                                </label>
                            </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="modal-footer bg-light">
                    <button type="button" class="btn btn-outline-dark " data-dismiss="modal">關閉</button>
                    <button type="button" class="btn btn-primary" @click.prevent="updateproudct">確認</button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
import Pagination from '../components/Pagination';

export default {
    data(){
        return{
            products: [],
            temProduct: {},
            isNew: false,
            isLoading: false,
            pagination: {},
            status: {
                fileLoading: false,
            },
        }
    },
    components:{
        Pagination,
    },
    methods:{
        getProducts(page = 1){
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/products?page=${page}`
            vm.isLoading = true;
            this.$http.get(api).then((response) => {
            vm.isLoading = false;
            vm.products = response.data.products;
            vm.pagination = response.data.pagination;
            console.log(vm.products)
            })
        },
        openModal(isNew,item){
            if(isNew){
                this.temProduct = {};
                this.isNew = true;
            }else{
                this.temProduct = Object.assign({},item);
                this.isNew = false;
            }
            $('#productModal').modal('show')
        },
        updateproudct(){
            const vm = this;
            let httpMethod = 'post';
            let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`
            if(!vm.isNew){
                api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.temProduct.id}`
                httpMethod = 'put';
            }
            this.$http[httpMethod](api,{data:vm.temProduct}).then((response) => {
                if(response.data.success){
                    $('#productModal').modal('hide')
                    vm.getProducts()
                }else{
                    $('#productModal').modal('hide')
                    vm.getProducts()
                    console.log('失敗');
                }
            })
        },
        removeProduct(id){
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${id}`
            vm.isLoading = true;
            this.$http.delete(api).then((response) => {
            vm.isLoading = false;
            vm.getProducts();
            })
        },
        uploadFile(){
            const loadedFile = this.$refs.files.files[0];
            const vm = this;
            const formData = new FormData();
            formData.append('file-to-upload',loadedFile);
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`
            vm.status.fileLoading = true
            this.$http.post(url,formData,{
                header:{
                    'Content-Type' : 'multipart/form-data',
                }
            }).then((response) => {
                vm.status.fileLoading = false;
                if(response.data.success){
                    vm.$set(vm.temProduct,'imageUrl',response.data.imageUrl);
                }else {
                    console.log(response.data.message)
                    this.$bus.$emit('messsage:push',response.data.message,'danger');
                }
            })
        }
    },
    created(){
        this.getProducts();
    }
}
</script>